import React from 'react'

const CustomerDetails = (props) => {
  const uppName = props.saleData ? props.saleData.clientName.charAt(0).toUpperCase() + props.saleData.clientName.slice(1) : "";
  return (
    <div className='customer-details'> 
    <div className="shop-name">Billed to :</div>

      <div className="">{uppName}</div>
      <div className="shop-phone">{props.saleData.clientContact}</div>
      <div className="shop-address">{props.saleData.clientAddress}</div>
    </div>
  )
}

export default CustomerDetails